
.newsMenu-wrapper {
  width: 92%;
  overflow: hidden;
  height: 1.1733rem;
  font-size: 0;
  margin: 0 auto;
}
.newsMenu-wrapper > .slide-box {
  height: 100%;
  margin: 0 auto;
  word-break: keep-all;
}
.newsMenu-wrapper > .slide-box > .center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.newsMenu-wrapper > .slide-box > .center > .item {
  display: inline-block;
  padding: 0 0.35rem 0 0;
  font-size: 16px;
  line-height: 1.1733rem;
}
.newsMenu-wrapper > .slide-box > .center > .item.active {
  color: #d5393c;
}


