
.headerComponent-wrapper {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  background: #fff;
}
.headerComponent-wrapper > .header {
  position: relative;
  width: 100%;
  height: 1.17333rem;
  line-height: 1.17333rem;
  background: #3a99f0;
  text-align: center;
  color: #fff;
}
.headerComponent-wrapper > .header > a {
  color: #fff;
}
.headerComponent-wrapper > .header > a > .icon-left {
  position: absolute;
  left: 0.48rem;
}
.headerComponent-wrapper > .header > .text {
  font-size: 18px;
  display: inline;
}
.headerComponent-wrapper > .state-wrapper {
  padding: 0.36rem 0;
  background: #3a99f0;
  font-size: 0;
}
.headerComponent-wrapper > .state-wrapper>.state-item {
  display: inline-block;
  width: 33%;
  text-align: center;
}
.headerComponent-wrapper > .state-wrapper>.state-item:not(:last-child) {
  border-right: 1px solid #fff;
}


