
.workRecord-wrapper {
  position: fixed;
  z-index: 101;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  background: #f0f0f0;
  font-size: 0;
}
.workRecord-wrapper > .body > #calendar-container {
  margin-bottom: 1.5rem;
}
.workRecord-wrapper > .body > .nav-item {
  display: block;
}
.workRecord-wrapper > .body > .nav-item:first-child {
  margin-top: 1rem;
}
.workRecord-wrapper > .body > .nav-item > .tab-wrapper:nth-child(1) {
  margin-top: 0.2rem;
}
/*日历插件中样式*/
/*考勤通过的日子*/
.calendar-active {
  color: #000;
}
.calendar-active > i {
  background: #f8994a;
}
/*不在当前视图中考勤通过的日子*/
#calendar-container > .calendar-block > .calendar-title {
  background: #3a99f0;
  color: #fff;
}
#calendar-container > .calendar-block > .calendar-title > span {
  color: #fff;
}
.calendar-item-body li {
  background: #3a99f0 !important;
  font-size: 12px;
  color: #fff !important;
}
/*日一二三四五六*/
.calendar-item {
  background: #3a99f0 !important;
  color: #f0f0f0 !important;
}
.calendar-item > .calendar-item-title span {
  font-size: 12px;
  color: #fff !important;
}


