
.header-wrapper {
  position: relative;
  width: 100%;
  height: 1.17333rem;
  line-height: 1.17333rem;
  background: #3a99f0;
  text-align: center;
  color: #fff;
}
.header-wrapper > a {
  color: #fff;
}
.header-wrapper > a > .icon-left {
  position: absolute;
  left: 0.48rem;
}
.header-wrapper > .text {
  font-size: 18px;
  display: inline;
}


