
.personal-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  left: 0;
  background: #f0f0f0;
}
.personal-wrapper > .header {
  position: fixed;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  height: 1.1733rem;
  background: #3a99f0;
  font-size: 0;
  color: #fff;
}
.personal-wrapper > .header > section {
  display: inline-block;
  line-height: 1.1733rem;
}
.personal-wrapper > .header > section.icon-sec {
  padding: 0 0.48rem;
}
.personal-wrapper > .header > .title {
  font-size: 18px;
}
.personal-wrapper > .body {
  padding-top: 1.1733rem;
}
.personal-wrapper > .body > .body-item {
  position: relative;
  padding: 0.28rem 0.56rem;
  margin-top: 0.48rem;
  margin-bottom: 1rem;
  background: #fff;
  font-size: 0;
}
.personal-wrapper > .body > .body-item > .avator {
  display: inline-block;
  width: 1.73333rem;
  height: 1.73333rem;
  border-radius: 50%;
  margin-right: 0.24rem;
  vertical-align: top;
}
.personal-wrapper > .body > .body-item > .personal-card {
  display: inline-block;
  padding: 0.3833rem 0.1rem;
}
.personal-wrapper > .body > .body-item > .personal-card > .name {
  font-size: 16px;
  margin-bottom: 0.2rem;
}
.personal-wrapper > .body > .body-item > .personal-card > .star-level > .text {
  font-size: 12px;
  display: inline-block;
}
.personal-wrapper > .body > .body-item > .personal-card > .star-level > .star-wrapper {
  display: inline-block;
}
.personal-wrapper > .body > .body-item > .icon {
  position: absolute;
  right: 0.48rem;
  top: 40%;
  color: #ccc;
}
.personal-wrapper > .body > .tab-box {
  display: block;
}
.personal-wrapper > .body > .tab-box:nth-child(2n+1) {
  margin-top: 1rem;
}
.personal-wrapper > .body > .tab-box:nth-child(2n) {
  margin-top: 0.2rem;
}


