
.tab-wrapper {
  position: relative;
  width: 100%;
  height: 1.17333rem;
  line-height: 1.17333rem;
  background: #fff;
  font-size: 0;
}
.tab-wrapper > .icon-box {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 0.8rem;
  height: 0.8rem;
  margin-left: 0.48rem;
  margin-right: 0.22rem;
  background: #f00;
  border-radius: 15%;
}
.tab-wrapper > .icon-box.green {
  background: #6cb648;
}
.tab-wrapper > .icon-box.degreen {
  background: #29b38b;
}
.tab-wrapper > .icon-box.red {
  background: #ed647b;
}
.tab-wrapper > .icon-box.purple {
  background: #ac6cec;
}
.tab-wrapper > .icon-box.grew {
  background: #617090;
}
.tab-wrapper > .icon-box > .icon {
  font-size: 22px;
  color: #fff;
}
.tab-wrapper > .tab-name {
  font-size: 16px;
  display: inline-block;
}
.tab-wrapper > .icon {
  color: #ccc;
  position: absolute;
  right: 0.48rem;
}


