
.navigator {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.306667rem;
  background: #3a99f0;
  font-size: 0;
}
.navigator > .navigator-item {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-align-items: center;
  width: 25%;
  height: 100%;
  text-align: center;
  color: #fff;
}
.navigator > .navigator-item > .box {
  color: #fff;
}
.navigator > .navigator-item > .box.router-link-exact-active {
  color: #0ff;
}
.navigator > .navigator-item > .box.router-link-active {
  color: #4bf8f9;
}
.navigator > .navigator-item > .box > .icon {
  font-size: 0.5866667rem;
}
.navigator > .navigator-item > .box > .text {
  margin-top: 0.1rem;
  font-size: 12px;
}


